export type PluginFactory<T, R> = (config: T) => R;

/**
 * 合并两个 PluginFactory
 * @param factory1
 * @param factory2
 */
export function combinePluginFactory<C1, R1, C2, R2>(
  factory1: PluginFactory<C1, R1>,
  factory2: PluginFactory<C2, R2>,
): PluginFactory<C1 & C2, R1 & R2> {
  function createCombinedPlugin(config: C1 & C2, ...rest: any[]) {
    // @ts-ignore
    const plugin1 = factory1(config, ...rest);
    // @ts-ignore
    const plugin2 = factory2(config, ...rest);
    return {
      ...plugin1,
      ...plugin2,
    };
  }
  return createCombinedPlugin;
}

/**
 * 合并两组 plugins，两组 plugins 交集的部分会合并，非交集的部分将独立存在
 * @param plugins1
 * @param plugins2
 */
export function combinePluginsFactory<C1, R1, C2, R2>(
  plugins1: Record<string, PluginFactory<C1, R1>>,
  plugins2: Record<string, PluginFactory<C2, R2>>,
): Record<string, PluginFactory<C1 & C2, R1 & R2>> {
  const plugins: Record<string, PluginFactory<C1 & C2, R1 & R2>> = {};

  Object.keys(plugins1).forEach((key) => {
    const factory1 = plugins1[key];
    const factory2 = plugins2[key];

    // @ts-ignore
    if (factory2 && factory1 !== factory2) {
      // 如果两个 plugins 同时存在一个 key 的插件，则合并
      plugins[key] = combinePluginFactory(factory1, factory2);
    } else {
      // plugin1 独有
      // @ts-ignore
      plugins[key] = factory1;
    }
  });

  Object.keys(plugins2).forEach((key) => {
    const factory1 = plugins1[key];
    const factory2 = plugins2[key];

    // 由于上面 plugin1 已经 combine，所以这里只需要取 plugin2 独有即可
    if (!factory1) {
      // plugin2 独有
      // @ts-ignore
      plugins[key] = factory2;
    }
  });

  return plugins;
}
